import { ActionTree } from 'vuex'
import { Product, CartItem, ProductState, RootState, CommitFunction } from '../types'
// import router from '@/router'
import Axios, { AxiosError, AxiosResponse } from 'axios'

export const actions: ActionTree<ProductState, RootState> = {
    async getProducts({ commit }: CommitFunction) {
        const url = "https://my-json-server.typicode.com/Nelzio/ecommerce-fake-json/products"
        Axios
            .get(url)
            .then((response: AxiosResponse) => {
                let products: Product[] = []
                response.data.forEach((item:any) => {
                    const detail = {
                        id: item.id,
                        name: item.name,
                        description: item.content,
                        active: true,
                        images: [item.imageUrl],
                        price: item.price
                    }
                    products.push(detail)                        
                });
                commit('setProductList', products)
            })
            .catch((error: AxiosError) => {
                console.log(error.message)
                commit('setProductError')
            })
    },
    async getDetails({ commit }: CommitFunction, id) {
        const url = "https://my-json-server.typicode.com/Nelzio/ecommerce-fake-json/products"
        Axios
            .get(url, { params: { id: id } })
            .then((response: AxiosResponse) => {
                const item = response.data[0]
                const detail = {
                    id: item.id,
                    name: item.name,
                    description: item.content,
                    active: true,
                    images: [item.imageUrl],
                    price: item.price
                }
                commit('setProduct', detail)
            })
            .catch((error: AxiosError) => {
                console.log(error.message)
                commit('setProductError')
            })
    },
    cartAddItem({ commit }: CommitFunction, payload: CartItem) {
        let cart:CartItem[] = this.getters.hasCart?this.getters.getCart:[]
        cart.push(payload)
        commit('setCart', cart)
    },
    cartRemoveProduct({ commit }: CommitFunction, payload: Product) {
        let cart:CartItem[] = this.getters.hasCart?this.getters.getCart:[]
        let newCart:CartItem[] = []
        if (payload) {
            for (let index = 0; index < cart.length; index++) {
                const element = cart[index];
                if (element.product.id !== payload.id) {
                    newCart.push(element)
                }
            }
        }
        commit('setCart', newCart)
    },
    cartRemoveAll({ commit }: CommitFunction) {
        let newCart:CartItem[] = []
        commit('setCart', newCart)
    },
    cartChangeItemQuantity({ commit }: CommitFunction, payload: CartItem) {
        let cart:CartItem[] = this.getters.hasCart?this.getters.getCart:[]
        let newCart:CartItem[] = []
        if (payload) {
            for (let index = 0; index < cart.length; index++) {
                const element = cart[index];
                if (element.product.id !== payload.product.id) {
                    newCart.push(element)
                } else {
                    element.quantity = payload.quantity
                    newCart.push(element)
                }
            }
        }
        commit('setCart', newCart)
    }
}
